import 'swiper/css';
import 'swiper/css/pagination';
import Swiper, { Pagination } from 'swiper';
import './scss/index.scss';

Swiper.use({ Pagination });

const variables = {};

const swiper = () => {
  const breakpoint = window.matchMedia('(min-width:580px)');
  let brandsSwiper, peripherySwiper;
  let isActiveSwiper = false;

  const breakpointChecker = () => {
    if (breakpoint.matches && isActiveSwiper) {
      deleteSwiper(brandsSwiper);
      deleteSwiper(peripherySwiper);
      return (isActiveSwiper = false);
    } else if (!breakpoint.matches) {
      brandsSwiper = createSwiper('.brands__swiper');
      peripherySwiper = createSwiper('.periphery__swiper');
      return (isActiveSwiper = true);
    }
  };

  function deleteSwiper(swiper) {
    swiper.destroy();
  }

  function createSwiper(container) {
    const newSwiper = new Swiper(container, {
      modules: [Pagination],
      loop: true,
      slidesPerView: 'auto',
      spaceBetween: 16,
      autoplay: {
        delay: 3000,
        disableOnInteraction: false,
      },
      pagination: {
        el: '.swiper__pagination',
        type: 'bullets',
        clickable: true,
      },
    });

    return newSwiper;
  }

  breakpoint.addEventListener('change', breakpointChecker);
  breakpointChecker();
};

const burgerMenu = () => {
  const burgerButton = document.querySelector('.burger-menu');
  const overlay = document.querySelector('.overlay');
  const aside = document.querySelector('.aside');
  const asideButtonClose = aside.querySelector('.aside__button-close');

  const openMenu = () => {
    aside.classList.add('aside--active');
    overlay.classList.add('overlay--active');
  };

  const closeMenu = () => {
    aside.classList.remove('aside--active');
    overlay.classList.remove('overlay--active');
  };

  burgerButton.addEventListener('click', openMenu);
  asideButtonClose.addEventListener('click', closeMenu);

  overlay.addEventListener('click', (event) => {
    if (event.target.classList.contains('overlay')) {
      closeMenu();
    }
  });
};

const addListenerForButtonSection = (
  sectionName,
  sectionList,
  sectionButtonShowContent,
  about = false
) => {
  const sectnList = document.querySelector(sectionList);
  const sectnButtonShowContent = document.querySelector(
    sectionButtonShowContent
  );
  const sectnButtonShowContentImg = sectnButtonShowContent.firstElementChild;
  const sectnButtonShowContentText = sectnButtonShowContent.lastElementChild;

  variables[`${sectionName}Container`] = sectnList;
  variables[`${sectionName}Button`] = sectnButtonShowContent;

  const sectnContent = toggleConditionContent();

  sectnButtonShowContent.addEventListener('click', () => {
    if (!sectnContent.activeContent) {
      sectnContent.showContent(
        sectnList,
        sectnButtonShowContentImg,
        sectnButtonShowContentText
      );
    } else {
      if (about) {
        sectnContent.hideContent(
          sectnList,
          sectnButtonShowContentImg,
          sectnButtonShowContentText,
          true
        );
      } else {
        sectnContent.hideContent(
          sectnList,
          sectnButtonShowContentImg,
          sectnButtonShowContentText
        );
      }
    }
  });
};

const toggleConditionContent = () => ({
  activeContent: false,
  showContent(container, imgButton, textButton) {
    const fullHeight = container.scrollHeight;

    container.style.height = `${fullHeight}px`;
    imgButton.style.transform = 'scale(1, -1)';
    textButton.textContent = 'Скрыть все';

    this.activeContent = true;
  },
  hideContent(container, imgButton, textButton, aboutContent) {
    if (aboutContent) {
      textButton.textContent = 'Читать далее';
    } else {
      textButton.textContent = 'Показать все';
    }
    container.style.height = '';
    imgButton.style.transform = '';

    this.activeContent = false;
  },
});

const toggleVisibilityButtons = () => {
  const hideButton = (button) => {
    button.style.display = 'none';
  };

  const showButton = (button) => {
    button.style.display = '';
  };

  const isEnoughHeight = (name) => {
    const fullHeight = variables[`${name}Container`].scrollHeight;
    const currentHeight = parseInt(
      window.getComputedStyle(variables[`${name}Container`]).height
    );

    if (currentHeight === fullHeight) {
      hideButton(variables[`${name}Button`]);
    } else {
      showButton(variables[`${name}Button`]);
    }
  };

  const checkSectionContentWidth = () => {
    isEnoughHeight('about');
    isEnoughHeight('brands');
    isEnoughHeight('periphery');
  };

  checkSectionContentWidth();

  const minWidth1000 = window.matchMedia('(min-width: 1000px)');

  minWidth1000.addEventListener('change', () => {
    if (minWidth1000.matches) {
      window.addEventListener('resize', checkSectionContentWidth);
    } else {
      window.removeEventListener('resize', checkSectionContentWidth);
    }
  });
};

const init = () => {
  swiper();
  burgerMenu();
  addListenerForButtonSection(
    'about',
    '.about__desc',
    '.about__button-show-content',
    true
  );
  addListenerForButtonSection(
    'brands',
    '.brands__list',
    '.brands__button-show-content'
  );
  addListenerForButtonSection(
    'periphery',
    '.periphery__list',
    '.periphery__button-show-content'
  );
  toggleVisibilityButtons();
};

init();
